.banner{
   
    background-size:cover;
    height: 560px;
    color: white;
    margin-top: 70px;
    
    
   
}

.content{
    padding-top: 140px;
    height:140px;
    padding-left: 15px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button{
    color: white;
    border: none;
    outline: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.2;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 380px;

}

.fade_bottom{
    padding-top: 165px;
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}